@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Card {
  font-size: 16px;
  margin: 0.6rem;
  width: calc(100%/5 - 1.25rem); }
  .Card__box {
    display: inline-block;
    position: relative; }
  .Card__image {
    width: 100%;
    border-radius: 0.3125rem;
    box-shadow: 0 0.125rem 0.1875rem 0.125rem #ccc; }
  .Card__box .Card__name {
    color: #FFFFFF;
    font-size: 1.5rem;
    position: absolute;
    margin: 0 auto;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 70%;
    z-index: 999; }
  @media screen and (max-width: 800px) {
    .Card {
      font-size: 12px;
      width: calc(100%/4 - 1.25rem); }
      .Card__box .Card__name {
        color: #FFFFFF;
        font-size: 1rem; } }
  @media screen and (max-width: 480px) {
    .Card {
      font-size: 8px;
      width: calc(100%/3 - 1.25rem); }
      .Card__box .Card__name {
        color: #FFFFFF;
        font-size: 1rem; } }

.GameStart {
  text-align: center; }
  .GameStart button {
    margin-top: 2rem;
    background-color: #316bd3;
    border: 2px solid #FFFFFF;
    color: #FFFFFF;
    text-transform: uppercase; }
  .GameStart button:hover {
    background-color: #FFFFFF;
    border: 2px solid #316bd3;
    color: #316bd3; }
  .GameStart__difficulty-radio-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center; }
    .GameStart__difficulty-radio-buttons__container {
      margin: 10px 20px 0 20px; }

.Scoreboard h3 {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  margin: 1.5rem auto; }

.App {
  text-align: center; }
  .App__card-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 15%; }
    @media screen and (max-width: 800px) {
      .App__card-container {
        margin: 0 4rem; } }
    @media screen and (max-width: 480px) {
      .App__card-container {
        margin: 0 2rem; } }

h1 {
  font-family: "Lato", sans-serif;
  margin-bottom: 0; }

h2 {
  font-family: "Lato", sans-serif;
  font-size: 1.2rem;
  font-weight: 300;
  margin: 1rem auto; }

button {
  background-color: #FFFFFF;
  border: 2px solid #316bd3;
  border-radius: 0.3rem;
  box-shadow: none;
  color: #316bd3;
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  padding: 0.7rem 2.5rem;
  margin-bottom: 2rem; }

button:hover {
  background-color: #316bd3;
  border: 2px solid #FFFFFF;
  color: #FFFFFF; }

